import React from 'react';
import logo from './logo.svg';
import './App.css';
import IDatepicker from './IDatepicker/idatepicker';
import moment from 'moment';

class App extends React.Component {
	constructor(props){
		super(props);

		this.cloudData = this.cloudData.bind(this);
	}

	cloudData(date){
		return moment().diff(date, 'days') === 0;	
	}

	render(){
	  return (
	    <div className="App">
	      	<IDatepicker cloudData={this.cloudData} />
	    </div>
	  );}
}

export default App;
