import React from 'react'
import "antd/dist/antd.css";
import { Input, Calendar, Tooltip, Icon, Alert  } from 'antd';
import moment from 'moment';

class IDatepicker extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
      date: moment(),
      showDatepicker: false,
      alert: false, 
      alertMessages: ''
    };

    this._onFocus = this._onFocus.bind(this);
    this._onBlur = this._onBlur.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.onPressEnter = this.onPressEnter.bind(this);
    this.onSelect = this.onSelect.bind(this);
    this._cloudData = this._cloudData.bind(this);
  }

  _cloudData(){
    if(this.props.cloudData)
      return this.props.cloudData(this.state.date);

    return false;
  }

  _onBlur(){
    setTimeout(() => { this.setState({showDatepicker: false})}, 200);
  }

  _onFocus(){
    this.setState({showDatepicker: true})
  }

  handleChange(event) {
    this.setState({value: event.target.value});
  }

  onPressEnter(){
    this.setState({showDatepicker: false})
    var value = this.state.value.toLowerCase();
    var validate = new RegExp('^([tyhwme])(-?)([0-9]+?)$', 'i');
    var number = new RegExp('(-?)([0-9]+?)$');
    var DATE_FORMATTER = 'DD-MMM-YYYY';

    if(validate.test(value)){
      var n = isNaN(parseInt(value.match(number))) ? 0 :  parseInt(value.match(number));
      var mode = '';
      var currentDate = moment();

      switch(value[0]) {
        case 't':
          mode = 'days';
          break;
        case 'e':
          mode = 'days';
          n += this.props.estimatedData ? this.props.estimatedData : 0;
          break;
        case 'y':
          n -= 1;
          mode = 'days';
          break;
        case 'w':
          mode = 'weeks';
          break;
        case 'm':
          mode = 'month';
          break;
        default:
          n = 0;
          mode = '';
          break;
      }

      currentDate = moment(currentDate).add(n, mode);
      this.setState({date: currentDate});
      this.setState({value: currentDate.format(DATE_FORMATTER)});
    } else {
      currentDate = moment(value);

      if(!currentDate.isValid()){
        this.setState({date: moment()});
        this.setState({value: moment().format(DATE_FORMATTER)});
      } else {
        this.setState({date: currentDate});
        this.setState({value: currentDate.format(DATE_FORMATTER)});
      }
    }
  }

  onSelect(value){
    var DATE_FORMATTER = 'DD-MMM-YYYY';
    this.setState({date: value})
    this.setState({value: value.format(DATE_FORMATTER)});
    this.setState({showDatepicker: false});
  }

  render() {
    return (
      <div cloudData={this._cloudData}> 
        { this.state.alert &&
          <Alert message={this.alertMessages} type="error" />
        }
        <Tooltip placement="right" title="Prompt Text" arrowPointAtCenter>
          <Input placeholder="" style={{fontWeight: 'bold'}} 
            onFocus={this._onFocus} 
            onBlur={this._onBlur} 
            value={this.state.value} 
            onChange={this.handleChange} 
            onPressEnter={this.onPressEnter} 
            suffix={<Tooltip>
                      <Icon type={this._cloudData() ? "cloud-sync" : "calendar"} style={{ color: 'rgba(0,0,0,.45)' }} />
                    </Tooltip>}
          />
        </Tooltip>
        { this.state.showDatepicker &&
          <div style={{ width: 300, border: '1px solid #d9d9d9', borderRadius: 4 }}>
            <Calendar fullscreen={false} onSelect={this.onSelect} />
          </div> 
        }
        
      </div>
    );
  }
}

export default IDatepicker;